'use client';
import React, { useState } from 'react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';

interface SearchResult {
  id: string;
  title: string;
  snippet: string;
  link: string;
}

const SearchPage: React.FC = () => {
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [results, setResults] = useState<SearchResult[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string>('');

  const handleSearch = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      const res = await fetch(`/api/search?query=${encodeURIComponent(searchTerm)}`);
      const data = await res.json();

      if (data.success) {
        setResults(data.results);
      } else {
        setError('No results found.');
      }
    } catch (err) {
      setError('An error occurred while fetching data.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 flex flex-col items-center justify-center p-6">
      <h1 className="text-5xl font-bold mb-8 text-gray-800">Search</h1>
      
      <form onSubmit={handleSearch} className="flex w-full max-w-xl mb-6">
        <Input
          type="text"
          placeholder="Search..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="flex-grow border border-gray-300 rounded-l-md p-4 shadow-md focus:outline-none focus:ring focus:ring-blue-500"
        />
        <Button type="submit" className="bg-blue-600 text-white rounded-r-md p-4 hover:bg-blue-700 transition duration-200">
          Search
        </Button>
      </form>

      {loading && <p className="mt-4 text-gray-600">Loading...</p>}
      
      {error && <p className="mt-4 text-red-500">{error}</p>}

      <div className="mt-6 w-full max-w-xl">
        {results.map((result) => (
          <div key={result.id} className="bg-white shadow-lg rounded-lg p-4 mb-4">
            <a href={result.link} target="_blank" rel="noopener noreferrer" className="text-blue-600 hover:underline">
              <h2 className="text-xl font-semibold">{result.title}</h2>
            </a>
            <p className="text-gray-700">{result.snippet}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default SearchPage;
